import React, { Component } from 'react';
import LightBox from './LightBox.js';

class MyWork extends Component {
  render() {
    return (
        <section id="work">
            <div className="container-fluid">
                <div className="row previouswork">
                    <div className="col-lg-3 col-md-3 col-sm-12 col-xs-12">
                        <h1 className="lng-eng">Recent Work:</h1>
                        <h1 className="lng-srb">Skorasnji radovi:</h1>
                    </div>
                    <div className="col-lg-9 col-md-9 col-sm-12 col-xs-12">
                        <article>
                            <p className="lng-eng">Since I haven't been active for a while, most of my work has either expired or still under NDA. Majoirty of my work comes from the period from 2012/2014 while I was working for <b>Heavyform creative agency/Hybrid studio/Eyeplay.be</b>.
                                The latest projects I was/am working on are under <b>Workcom.co</b> and <b>WoodsCreative.ca/Acropont.ca</b></p>
                        </article>
                        <article>
                            <p className="lng-srb">Posto se nisam bavio front-end web developmentom u skorasnje vreme vecina mojih projekata je ili istekla ili jos uvek pod NDA. Vecina projekata na kojima sam radio su iz perioda od 2012 do 2014. godine i radjeni su za <b>Heavyform
                                creative agency/Hybrid studio/Eyeplay.be</b>. A najnoviji poduhvati su vezani za <b>Workcom development platform</b>, <b>woodscreative.ca creative agency</b> i <b>acropont.ca</b>.</p>
                        </article>
                    </div>
                </div>
                <div className="row project-list">
                    <LightBox />
                </div>
            </div>
        </section>
    );
  }
}

export default MyWork;



