import React, { Component } from 'react';
import ReactDOM from 'react-dom';

class IntroCanvas extends Component {

  render() {
    return (
      <section id="intro">
        <div className="container-fluid">
            <div className="row">
              <canvas id="canvas"></canvas>
            </div>
        </div>
      </section>
    );
  }
}



export default IntroCanvas;




