import React, { Component } from 'react';

class Navigation extends Component {
  render() {
    return (
      <div className="row">
        <div className="col-lg-12 col-md-12X col-sm-12 col-xs-12" id="nav-anchor">
          <nav className="navbar navbar-default" id="navigation-main">
            <div className="container-fluid" id="container-nav">
              <div className="navbar-header">
                <button type="button" id="nav-toggle" className="navbar-toggle collapsed" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1" aria-expanded="false">
                <span className="sr-only">Toggle navigation</span>
                <span className="icon-bar"></span>
                </button>
              </div>
              <div className="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
                <ul className="nav navbar-nav">
                <li><a href="#intro" id="intro-btn" className="menu-buttons nav-active lng-eng">INTRO</a><a href="#intro" id="uvod-btn" className="menu-buttons nav-active lng-srb lng-display-none-nav">UVOD</a></li>
                <li><a href="#about" className="menu-buttons lng-eng" id="about-btn">ABOUT</a><a href="#about" className="menu-buttons lng-srb lng-display-none-nav" id="omeni-btn">O MENI</a></li>
                <li><a href="#work" className="menu-buttons lng-eng" id="mywork-btn">MY WORK</a><a href="#work" className="menu-buttons lng-srb lng-display-none-nav" id="mojiradovi-btn">MOJI RADOVI</a></li>
                <li><a href="#contactme" className="menu-buttons lng-eng" id="contact-us-btn">CONTACT</a><a href="#contactme" className="menu-buttons lng-srb lng-display-none-nav" id="kontakt-btn">KONTAKT</a></li>
                  <span className="my-language"><a href="#en" className="lng-active" id="lng-eng">EN</a> &nbsp;&nbsp;|&nbsp;&nbsp; <a href="#srb" id="lng-srb">SRB</a></span>
                </ul>
              </div>
            </div>
          </nav>
        </div>
      </div>
    );
  }
}

export default Navigation;



