import React, { Component, PropTypes } from 'react';
import FontAwesomeFacebook from './FontAwesomeFacebook.js';
import FontAwesomeTwitter from './FontAwesomeTwitter.js';
import FontAwesomeLinkedin from './FontAwesomeLinkedin.js';
import FontAwesomeTwitch from './FontAwesomeTwitch.js';
import FontAwesomeGoogle from './FontAwesomeGoogle.js';

class SocialLinks extends React.Component {
    
  render() {
    
    const linksSocial = [
      { "class": "twit", "class2": <FontAwesomeTwitter />, "description": " lazy tweet me", "link": "https://twitter.com/intent/user?screen_name=VladimirCordas", "script": <script async src="//platform.twitter.com/widgets.js" charSet="utf-8"></script>},
      { "class": "face", "class2": <FontAwesomeFacebook />, "description": " tell your friends", "link": "https://www.facebook.com/sharer/sharer.php?u=http%3A%2F%2Fjustanothergenericportfolio.freeiz.com%2F"},
      { "class": "linkdin", "class2": <FontAwesomeLinkedin />, "description": " I'm smart, I swear", "link": "https://www.linkedin.com/in/vladimir-cordas-8972b14a/"},
      { "class": "twitcher", "class2": <FontAwesomeTwitch />, "description": " watch the guy play", "link": "https://www.twitch.tv/addictionjukebox"},
      { "class": "googleplus", "class2": <FontAwesomeGoogle />, "description": " who checks this?", "link": "https://plus.google.com/u/0/+VladimirCordas"}
    ];

      const socialLinks = linksSocial.map((linksSocial) =>
        <li>
          <a href={linksSocial.link} target="_blank">
            <button className={linksSocial.class}>
              {linksSocial.class2}
              {linksSocial.description}
            </button>
          </a>
          {linksSocial.script}
        </li>
      );
        
      return (
        <ul className="links" id="social">
          {socialLinks}
        </ul>
      );
    }
  }
  
export default SocialLinks;


