var React = require('react');
var FontAwesome = require('react-fontawesome');

var FontAwesomeLinkedin = React.createClass({
  render: function () {
    return (
      <FontAwesome
        className='icons'
        name='linkedin'
        style={{ textShadow: '0 1px 0 rgba(0, 0, 0, 0.1)' }}
        tag="i"
      />
    );
  }
});

export default FontAwesomeLinkedin;


