import React, { Component, PropTypes } from 'react';

class SpareTime extends React.Component {
    
  
  render() {
    
    const spareTime = [
      { "link": "The CSS3 Anthology", "author": "by Rachel Andrew", "location": "https://www.sitepoint.com/premium/books/the-css3-anthology-4th-edition"},
      { "link": "Responsive Web Design with HTML5 and CSS3", "author": "by Ben Frain", "location": "https://www.amazon.com/Responsive-Web-Design-HTML5-CSS3/dp/1849693188"},
      { "link": "Coding with JavaScript For Dummies", "author": "by Ben Frain", "location": "https://www.amazon.co.uk/Coding-JavaScript-Dummies-Chris-Minnick-x/dp/1119056071"},
      { "link": "JavaScript Essentials", "author": "by Smashing Magazine", "location": "https://www.smashingmagazine.com/learning-javascript-essentials-guidelines-tutorials/"},
      { "link": "HTML5 Canvas, 2nd Edition", "author": "by Steve & Jeff Fulton", "location": "https://www.amazon.com/HTML5-Canvas-Native-Interactivity-Animation/dp/1449334989"},
      { "link": "Forgotten realms books", "author": "by R.A. Salvatore & Elaine Cunningham", "location": "http://www.bookseriesinorder.com/r-a-salvatore/"},
      { "link": "Dresden files novels", "author": "by Jim Butcher", "location": "http://www.jim-butcher.com/books/dresden"},
      { "link": "Shannara books", "author": "by Terry Brooks", "location": "https://www.goodreads.com/series/99160-shannara---terry-s-suggested-order-for-new-readers"},
      { "link": "Lineage 2 - bartz server", "author": "NC Soft", "location": "http://www.lineage2.com/en/"}
    ];

      const spareTimeList = spareTime.map((spareTime) =>
        <li>
          <a target="_blank" href={spareTime.location}>{spareTime.link}</a>
          <br />
          <span className="authors">{spareTime.author}</span>
        </li>
      );
    
     
        
      return (
        <ul className="links">
          {spareTimeList}
        </ul>
      );
    }
  }
 
  
export default SpareTime;


