import React, { Component, PropTypes } from 'react';

class UsefullLinks extends React.Component {
    
  
  render() {
    
    const linksUsefull = [
      { "link": "udemy.com", "linkLocation": "https://www.udemy.com/", "description": "- Udemy interactive courses"},
      { "link": "lynda.com", "linkLocation": "https://www.lynda.com/", "description": "- Lynda interactive courses"},
      { "link": "CodeAcademy", "linkLocation": "https://www.codecademy.com/", "description": "- CodeAcademy, learn to code"},
      { "link": "FreeCodeCamp", "linkLocation": "https://www.freecodecamp.org/", "description": "- Learn to code for free!"},
      { "link": "SitePoint", "linkLocation": "https://www.sitepoint.com/", "description": "- Everything you need..."},
      { "link": "9gag", "linkLocation": "https://9gag.com/", "description": "- memes, cause why not?"},
      { "link": "Chess not checkers", "linkLocation": "https://www.chess.com/", "description": "- These kids are good..."},
      { "link": "Heavyform", "linkLocation": "http://www.heavyform.com/", "description": "- Where my journey began."}
    ];

      const usefullLinks = linksUsefull.map((linksUsefull) =>
        <li>
          <a target="_blank" href={linksUsefull.linkLocation}>{linksUsefull.link}</a>
          <span className="description">{linksUsefull.description}</span>
        </li>
      );
    
     
        
      return (
        <ul className="links">
          {usefullLinks}
        </ul>
      );
    }
  }
 
  
export default UsefullLinks;


